export const fetchCsrfToken = async () => {
  try {
    const securityContext = "VPLMAdmin.Company Name.Default";
    const WAFData = await loadWAFData(); // Load the WAFData module

    // üîµ Step 1: Get login token
    const loginTokenUrl =
      "https://oi000186152-eu1.iam.3dexperience.3ds.com/login?action=get_auth_params";
    const loginTokenResponse = await fetch(loginTokenUrl);

    if (!loginTokenResponse.ok) {
      throw new Error(
        `Failed to fetch login token. Status: ${loginTokenResponse.status}`
      );
    }

    const loginData = await loginTokenResponse.json();
    const loginToken = loginData?.lt;

    if (!loginToken) {
      console.warn("[Login] Token missing. Aborting further steps.");
      return null;
    }

    console.log("[Login] Token:", loginToken);

    // üîµ Step 2: Define CSRF service URL
    const csrfServiceUrl =
      "https://oi000186152-us1-space.3dexperience.3ds.com/enovia/resources/v1/application/CSRF?tenant=OI000186152";

    // üîµ Step 3: Compose final login URL
    const loginUrl = `https://oi000186152-eu1.iam.3dexperience.3ds.com/login?lt=${loginToken}&username=emrserviceuser&password=Emerson123&service=${csrfServiceUrl}`;
    console.log("Final loginUrl:", loginUrl);

    // üîµ Step 4: Call login URL using WAFData
    const response = await fetch(loginUrl, {
      method: "GET",
      credentials: "include",
    });
    const json = await response.json();
    console.log("JSON is:", json);

    if (!response || !response.csrf) {
      console.error("Login succeeded, but CSRF token not found in response");
      return null;
    }

    // ‚úÖ Step 5: Extract CSRF and construct headers
    const csrfToken = response.csrf.name;
    const csrfValue = response.csrf.value;

    const headers = {
      [csrfToken]: csrfValue,
      SecurityContext: securityContext,
      "Content-Type": "application/json",
    };

    console.log("[CSRF] Token fetched and headers ready:", headers);
    return headers;
  } catch (error) {
    console.error("[CSRF] Failed to fetch token:", error);
    return null;
  }
};
