import React, { useEffect, useState } from "react";
import DragAndDropComponent from "../DragAndDrop/DragAndDrop";
import { fetchChangeActionFlow } from "./utils/fetchChangeActionFlow";

const App = () => {
  const [iframeUrl, setIframeUrl] = useState("");
  const [error, setError] = useState("");
  useEffect(() => {
    // 1. Reset function
    window.resetToDropArea = () => {
      console.log("[App] Resetting to Drop Area View");
      setIframeUrl("");
      setError("");
    };

    // 2. Setup manual refresh tracking
    let manualRefresh = false;

    const mousedownListener = (event) => {
      const parentDoc = window.parent?.document;
      if (!parentDoc) return;

      const dropdown = parentDoc.querySelector(".widget-menu-icon");
      const refreshButton = event.target.closest(".refresh-item");

      if (dropdown && dropdown.contains(event.target)) {
        console.log("[App] Dropdown opened.");
        manualRefresh = false; // Reset on new dropdown open
      }

      if (refreshButton && refreshButton.textContent.trim() === "Refresh") {
        console.log("[App] User manually clicked Refresh.");
        manualRefresh = true;
      }
    };

    window.parent.document.body.addEventListener(
      "mousedown",
      mousedownListener,
      true
    );

    // 3. Attach onRefresh from widget
    if (window.widget && typeof window.widget.addEvent === "function") {
      window.widget.addEvent("onRefresh", () => {
        console.log(`[App] üîÑ onRefresh triggered | Manual: ${manualRefresh}`);

        if (manualRefresh && typeof window.resetToDropArea === "function") {
          window.resetToDropArea();
        }

        // Always reset the flag after refresh
        manualRefresh = false;
      });
    }

    // 4. Cleanup
    return () => {
      window.parent.document.body.removeEventListener(
        "mousedown",
        mousedownListener,
        true
      );
    };
  }, []);

  const handleDropSuccess = async (obj) => {
    try {
      const item = obj?.data?.items?.[0];
      if (!item) throw new Error("Dropped item is missing");

      const { objectType, objectId } = item;

      if (objectType !== "Change Action") {
        alert("This functionality is not available for selected type");
        return;
      }

      const caFlowResponse = await fetchChangeActionFlow(objectId);

      const flowDownArray = caFlowResponse?.isFlowDownOf;

      if (flowDownArray && flowDownArray.length > 0) {
        const targetId = flowDownArray[0].identifier;
        const iUrl = `https://emr-product-datahub-qa.azurewebsites.net/caDetails/${targetId}`;
        setIframeUrl(iUrl);
      } else {
        const iUrlFallback = `https://plmpdh-qa.emerson.com/caDetails/${objectId}`;
        setIframeUrl(iUrlFallback);
      }
    } catch (err) {
      console.error("Drop handler failed:", err);
      setError(err.message || "Unknown error");
    }
  };

  if (error) {
    return <div className="error">{error}</div>;
  }

  return (
    <div>
      {!iframeUrl ? (
        <DragAndDropComponent onDropSuccess={handleDropSuccess} />
      ) : (
        <iframe
          src={iframeUrl}
          title="CA Details"
          style={{ width: "100vw", height: "100vh", border: "none" }}
        />
      )}
    </div>
  );
};

export default App;
